import { createGlobalStyle } from 'styled-components';

export const GlobalStyle = createGlobalStyle`

html{
  box-sizing: border-box;
}

*, ::after, ::before {
  box-sizing: inherit;
}

body {
  font-family: 'Lato', sans-serif;
  background: ${({ theme }) => theme.color.gallery};
  font-size: 16px;
  padding: 10px;
}

`;
